import "./App.css";
import Header from "./components/Header";
import Tasks from "./components/Tasks";
import AddTask from "./components/AddTask";
import { useState } from "react";
function App() {
  const [showAddTask, setShowAddTask] = useState(false);
  const [tasks, setTasks] = useState([
    { id: 1, text: "Docktor appointment", done: true },
    { id: 2, text: "get appels", done: false },
  ]);

  const toggleComplete = (id) => {
    setTasks(tasks.map((task) => (task.id === id ? { ...task, done: !task.done } : task)));
  };
  const addTask = (task) => {
    const id = Math.floor(Math.random() * 1000 + 1);
    const newTask = { id, ...task };
    setTasks([...tasks, newTask]);
    console.log(newTask);
  };
  const onDelete = (id) => {
    setTasks(
      tasks.filter((task) => {
        return task.id !== id;
      })
    );
  };

  return (
    <div className="container">
      <Header onAdd={() => setShowAddTask(!showAddTask)} showAdd={showAddTask} title="App Tracker" />
      {showAddTask && <AddTask onAdd={addTask} />}

      {tasks.length > 0 ? <Tasks onDelete={onDelete} toggleComplete={toggleComplete} tasks={tasks} /> : "No tasks to show"}
    </div>
  );
}

export default App;
